import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {ReportResolverService} from './shared/report-resolver.service';
import {IndexComponent} from './index/index.component';
import {CreateReportComponent} from './create-report/create-report.component';
import {CreateReportResolverService} from './shared/create-report-resolver.service';
import {CancelInvoiceResolverService} from '@modules/handle-report/shared/cancel-invoice-resolver.service';
import {CancelReportComponent} from '@modules/handle-report/cancel-report/cancel-report.component';


const routes: Routes = [
  {path: 'cancel-report/:id', component: CancelReportComponent, resolve: {CancelInvoiceResolverService}},
  {path: ':id/:status', component: IndexComponent, resolve: {ReportResolverService}},
  {path: 'create-report/:id/:status', component: CreateReportComponent, resolve: {CreateReportResolverService}},
  {path: '**', redirectTo: '/'},
  {path: '/:status', component: IndexComponent, resolve: {ReportResolverService}},

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class routing {
}
