import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';
import {HanldeReportService} from '@modules/handle-report/hanlde-report.service';
import {BsModalRef} from 'ngx-bootstrap';
import {validate} from 'codelyzer/walkerFactory/walkerFn';
import {Library} from '@core/library';
import {finalize} from 'rxjs/operators';
import {AuthService} from '@core/services';
import {TranslateService} from '@ngx-translate/core';

@Component({
  selector: 'app-send-report',
  templateUrl: './send-report.component.html',
  styleUrls: ['./send-report.component.scss']
})
export class SendReportComponent implements OnInit {
  form: FormGroup;
  id: any;
  data: any;
  emails: any = [];
  private arrEmail: any = [];
  submitted = false;
  checkSubmit: any = false;
  dataEmail: any;
  checkedInputEmail = true;
  _translate: any;
  _language: string;

  constructor(
    private authService: AuthService,
    public translate: TranslateService,
    private handleReportService: HanldeReportService,
    private fb: FormBuilder,
    public bsModalRef: BsModalRef,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    this.translate.use(this._language);
  }

  ngOnInit() {
    this.form = this.fb.group(this.handleReportService.formCancel);
    this.form.patchValue({
      id: this.id,
      email: this.dataEmail
    });
    this.emails = [];
    if (this.dataEmail !== null) {
      const stringDataEmailGet = this.dataEmail.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.emails.push({display: stringDataEmailGet[i], value: stringDataEmailGet[i]});
      }
    }
  }

  renderDataEmail($event) {
    this.arrEmail = [];
    this.emails = $event;
    if (this.emails.length > 0) {
      for (let i = 0; i < this.emails.length; i++) {
        if (typeof this.emails[i] === 'object')
        this.arrEmail.push(this.emails[i].value);
      else
        if (typeof this.emails[i] === 'string')
          this.arrEmail.push(this.emails[i]);
      }
      const emailToString = this.arrEmail.toString();
      this.dataEmail = emailToString.replace(/,/g, ';');
    } else {
      this.dataEmail = null;
    }
  }

  submit() {
    if (this.form.get('cancel_reason').invalid) {
      this.submitted = true;
      return;
    }
    if (this.checkedInputEmail === true) {
      this.form.patchValue({
        email: this.dataEmail
      });
      if (this.form.get('email').invalid) {
        this.submitted = true;
        return;
      }
    } else {
      this.form.patchValue({
        email: null
      });
    }
    if (this.submitted === true) {
      Library.notify(this._translate.NOTIFY.enter_full_information, 'error');
    }
    this.submitted = false;
    this.checkSubmit = true;
    Library.showloading();
    this.handleReportService.apiCancelReport(this.form.value)
      .pipe(finalize(() => Library.hideloading()))
      .subscribe(value => {
          if (value.status === 'success') {
            Library.notify(this._translate.NOTIFY.cancel_report_done, 'success');
            this.bsModalRef.content.successSaveItem = 'SuccessSendReport';
            this.bsModalRef.hide();
          } else {
            Library.notify(value.message, 'error');
          }
        },
        error => {
          Library.notify(error, 'error');
        }
      );
  }

  checkStatusEmail() {
    this.checkedInputEmail = !this.checkedInputEmail;
  }
}
