import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot} from '@angular/router';
import {Observable} from 'rxjs';
import {HanldeReportService} from '@modules/handle-report/hanlde-report.service';
import {Library} from '@core/library';
import {finalize, map} from 'rxjs/operators';
import {el} from '@angular/platform-browser/testing/src/browser_util';

@Injectable({
  providedIn: 'root'
})
export class CancelInvoiceResolverService implements Resolve<any[]> {
  constructor(
    private handleService: HanldeReportService,
    private router: Router,
  ) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> | Promise<any[]> | any[] {
    Library.showloading();
    const id = route.paramMap.get('id');
    const status = route.queryParams.status;
    return this.handleService.apiShowHDXoaBo(id, 1)
      .pipe(
        map(value => {
          if (value[2].status === 'success' && value[0].status === 'success' && value[1].status === 'success' && value[0].data.invoice.status === 'DA_XUAT' && value[0].data.invoice.adjustment_type !== '2' && value[0].data.invoice.adjustment_type !== '4' && value[0].data.invoice.adjustment_type !== '7') {
            const dataInvoice = value[0].data.invoice;
            const dataConfig = value[1].data.configs;
            const dataReport = value[2].data.invoice_reports;
            return [
              dataInvoice,
              dataConfig,
              dataReport
            ];
          } else {
            this.router.navigate(['/system/handle-report/', id, 'HUY']);
          }
        }),
        finalize(() => Library.hideloading())
      );
  }
}
