import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HanldeReportService } from '@modules/handle-report/hanlde-report.service';
import { map } from 'rxjs/operators';
import * as moment from 'moment';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Base64 } from 'js-base64';
import { Helper } from '@core/helper/Helper';
import { AuthService, SocketService } from '@core/services';
import { Library } from '@core/library';
import { Location } from '@angular/common';
import { InvoicesService } from '@core/library/invoice/invoices.service';
import { String } from 'typescript-string-operations';
import { TranslateService } from '@ngx-translate/core';
import { VChanggeMoneyService } from '@core/library/invoice/v-changge-money.service';
import { HandleInvoiceService } from '@modules/handle-invoice/shared';

@Component({
  selector: 'app-cancel-report',
  templateUrl: './cancel-report.component.html',
  styleUrls: ['./cancel-report.component.scss']
})
export class CancelReportComponent implements OnInit {
  id: any;
  myDateValue: any;
  date: Date = new Date();
  form: FormGroup;
  dataInvoice: any;
  emails: any = [];
  private arrEmail: any = [];
  submitted = false;
  formatMoney: any;
  ioSignature: any;
  coKiSoTapChung = false;
  isRemoteSignning = false;
  dataConfigs: any = [];
  checkedInputEmail = true;
  private emailString: any;
  fileData: Array<any> = [];
  system_parameter: any;
  _translate: any;
  dataReport: any = [];
  updateDateInvoice: any;
  compareDate: boolean;
  dataApi: any;
  _language: string;

  formatter: any = {};
  dataMoneyConfig: any = {};
  isSystem_parameter: any;
  accountType: string;
  nodeSign: any;
  isTT78: boolean = false;
  infoDangKy: any;
  configs: any;
  invoices: any = [];
  isCancelInvSucess: boolean = false;
  constructor(
    private authService: AuthService,
    private route: ActivatedRoute,
    private handleReport: HanldeReportService,
    private fb: FormBuilder,
    private socketService: SocketService,
    private router: Router,
    public translate: TranslateService,
    private handleInvoiceService: HandleInvoiceService,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    this.translate.use(this._language);
    this.accountType = this.authService.getAccountType();
  }

  ngOnInit() {
    this.configs = this.authService.getConfigs();
    this.form = this.fb.group(this.handleReport.generatedFormCancelInvoice());
    this.id = this.route.snapshot.paramMap.get('id');
    const currentUser = this.authService.getCurrentUser();
    this.route.data
      .pipe(map(value => value.CancelInvoiceResolverService))
      .subscribe(
        value => {
          this.dataInvoice = value[0];
          this.dataMoneyConfig = JSON.parse(this.dataInvoice.decimal_places_json);
          this.dataConfigs = value[1];
          this.dataReport = value[2];
          this.myDateValue = moment(value[0].invoice_date).format('DD/MM/YYYY');
          this.updateDateInvoice = this.dataInvoice.updated_at;
          this.infoDangKy = this.authService.getInfoRegister();
          if (this.infoDangKy) {
            if (this.infoDangKy.status == "CHAP_NHAN") {
              this.isTT78 = true;
            }
          }

          if (this.dataReport.length > 0) {
            this.emailString = this.dataReport[0].email;
            this.praseEmail(this.emailString);
            this.form.patchValue({
              invoice: {
                id: value[0].id,
                destroy_date: this.date,
                destroy_reason: this.dataReport[0].destroy_reason,
                email: this.emailString
              }
            });
          } else {
            this.emailString = this.dataInvoice.hasOwnProperty('email') ? this.dataInvoice.email : this.dataInvoice.buyer_email;
            this.praseEmail(this.emailString);
            this.form.patchValue({
              invoice: {
                id: value[0].id,
                destroy_date: this.date,
                email: this.emailString
              }
            });
          }
          this.dataConfigs.forEach(item => {
            if (item.name === 'integrate_parameter') {
              const config = JSON.parse(item.value);
              this.coKiSoTapChung = config.concentrate_signature.is_concentrate_signature;
              if (config.remote_signing && config.remote_signing.is_remote_signing) {
                this.isRemoteSignning = true;
              }
            }
            if (item.name === 'decimal_places_json') {
              this.formatter = VChanggeMoneyService.getFormatterColumnInv(JSON.parse(item.value));
            }
            if (item.name === 'system_parameter') {
              this.isSystem_parameter = JSON.parse(item.value);
            }
          });
          const _obj = this.dataConfigs.filter(p => p.name === 'system_parameter')[0];
          if (_obj) {
            this.system_parameter = _obj;
          }
          if (this.dataInvoice.total_payment) {
            this.formatMoney = (this.dataInvoice.total_payment).toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,');
          } else {
            this.formatMoney = '0';
          }
        },
        error => {
          Library.notify(error, 'error');
        }
      );
    if (this.dataInvoice.xml_type == "4") {
      this.isTT78 = false
    }
    this.connectWebsocket();
  }

  praseEmail(data) {
    this.emails = [];
    if (data !== null && data !== undefined) {
      const stringDataEmailGet = data.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
      }
    }
  }



  submit() {
    const _isValid = InvoicesService.checkTimeExportInv(this.system_parameter);
    if (!_isValid) {
      Library.notify(String.Format(this._translate.NOTIFY.time_export_inv, moment(this.system_parameter.time_start).format('HH:mm'), moment(this.system_parameter.time_end).format('HH:mm')), 'error');
      return;
    }

    if (this.checkedInputEmail === true) {
      this.form.patchValue({
        invoice: {
          email: this.emailString
        }
      });
      if (this.form.get('invoice.email').invalid) {
        this.submitted = true;
        return;
      }
    } else {
      this.form.patchValue({
        invoice: {
          email: null
        }
      });
    }
    if (this.fileData.length > 0) {
      this.form.patchValue({
        invoice_files: this.fileData
      });
    }

    this.submitted = false;
    this.form.patchValue({
      invoice: {
        destroy_date: moment(this.form.value.invoice.destroy_date).format('YYYY-MM-DD')
      }
    });
    if (this.dataReport.length > 0) {
      if (this.dataReport[0].doc_relate_date) {
        this.compareDate = new Date(moment(this.dataReport[0].doc_relate_date).format('YYYY-MM-DD')) <= new Date(moment(this.form.value.invoice.destroy_date).format('YYYY-MM-DD'));
        if (this.compareDate === false) {
          Library.notify(this._translate.NOTIFY.date_cancel_invoice_greater_report, 'error');
          return;
        }
      }

    } else {
      this.compareDate = new Date(moment(this.dataInvoice.invoice_date).format('YYYY-MM-DD')) <= new Date(moment(this.form.value.invoice.destroy_date).format('YYYY-MM-DD'));
      if (this.compareDate === false) {
        Library.notify(this._translate.NOTIFY.date_cancel_invoice_greater_invoice, 'error');
        return;
      }
    }

    setTimeout(() => {
      if (this.coKiSoTapChung || this.isRemoteSignning) {
        this.xuLiApiKhiXuat();
      } else {
        if (this.isTT78 && this.dataInvoice.xml_type != 1) {
          let ListSerialNumber = "DSCKS";
          if (this.infoDangKy && this.infoDangKy.registration_signature) {
            let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
            if (signatures && signatures.length > 0)
              ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
          }
          let invoiceDate = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
          this.socketService.kiSo(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice', invoiceDate, "", ListSerialNumber, true);
        }
        else
          this.socketService.kiSo(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice');
      }
    }, 300);
  }
  xuLiApiKhiXuat() {
    let data = {
      id: this.form.value.invoice.id,
      adjustment_type: "7",
      destroy_reason: this.form.value.invoice.destroy_reason,
      email: this.form.value.invoice.email,
      date: this.form.value.invoice.destroy_date
    };
    Library.showloading();
    let sub = this.handleReport.getXmlString(data).subscribe(rs => {
      if (rs.status == 'success') {
        let xmlString = rs.data.invoice.xml_string;
        let isNewXml = rs.data.invoice.is_new_xml;
        let sign_date = moment(rs.data.invoice.sign_date).format('YYYY-MM-DD HH:mm:ss');
        if (isNewXml)
          this.nodeSign = "//HDon/DSCKS/NBan";
        else
          this.nodeSign = "//inv:transaction/inv:invoices/inv:invoice";
        if (xmlString != "") {
          if (this.coKiSoTapChung || this.isRemoteSignning || this.dataInvoice.xml_type == 4) {
            this.form.patchValue({
              invoice: {
                xml_string: xmlString
              }
            });
            if (this.fileData.length > 0) {
              this.goiApiXoaHoaDon(this.form.value, true);
            } else {
              this.goiApiXoaHoaDon(this.form.value.invoice, false);
            }
          }
          else {
            let invoiceDate = moment(this.dataInvoice.invoice_date).format('YYYY-MM-DD HH:mm:ss');
            if (this.isTT78 && isNewXml) {
              let ListSerialNumber = "DSCKS";
              if (this.infoDangKy && this.infoDangKy.registration_signature) {
                let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
                if (signatures && signatures.length > 0)
                  ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
              }
              this.socketService.kiSo(() => this.connectWebsocket(), xmlString, 'hoadon.xml', this.nodeSign, sign_date, 'InvoiceData', ListSerialNumber, true);
            }
            else
              this.socketService.kiSo(() => this.connectWebsocket(), xmlString, 'hoadon.xml', this.nodeSign, invoiceDate);
          }
        }
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }
  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDon().subscribe(message => {
      const data: any = message;
      if (data.SvcName === 'efy_Sign') {
        if (data.Data.Name === 'hoadon.xml') {
          if (this.isCancelInvSucess)
            this.sendData_TBSaiSot(data.Data.Content);
          else {
            this.form.patchValue({
              invoice: {
                destroy_date: moment(this.form.value.invoice.destroy_date).format('YYYY-MM-DD'),
                xml_string: data.Data.Content
              }
            });
            setTimeout(() => {
              if (this.fileData.length > 0) {
                this.goiApiXoaHoaDon(this.form.value, true);
              } else {
                this.goiApiXoaHoaDon(this.form.value.invoice, false);
              }
            }, 400);
          }
        } else if (data.Data.Name === 'ten_filek_the_trung-dc-nhi.xml') {
          if (this.isCancelInvSucess)
            this.getXml_TBSaiSot();
          else {
            this.xuLiApiKhiXuat();
          }
        }
      }
    });
  }

  goiApiXoaHoaDon(data, file_DK) {
    if (file_DK) {
      this.dataApi = data;
    } else {
      this.dataApi = { invoice: data };
    }
    Library.showloading();
    const sub = this.handleReport.apiRemoveInvoice(this.dataApi).subscribe(rs => {
      if (rs.status === 'success') {
        this.isCancelInvSucess = true;
        if (this.configs.system_parameter.enable_send_wrong_cancel && this.isTT78) {
          if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
            || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
            Library.notify(this._translate.NOTIFY.cancel_invoice_done, 'success');
            this.router.navigate(['/system/handle-report/', this.id, 'HUY']);
          }
          else
            this.startProcess_TBSaiSot();
        }
        else {
          if (this.configs.system_parameter.enable_send_wrong_cancel && this.dataInvoice.xml_type == 4) {
            this.startProcess_TBSaiSot();
          } else {
            Library.notify(this._translate.NOTIFY.cancel_invoice_done, 'success');
            this.router.navigate(['/system/handle-report/', this.id, 'HUY']);
          }
        }
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }
  renderDataEmail($event) {
    this.arrEmail = [];
    this.emails = $event;

    if (this.emails.length > 0) {
      for (let i = 0; i < this.emails.length; i++) {
        if (typeof this.emails[i] === 'object')
          this.arrEmail.push(this.emails[i].value);
        else
          if (typeof this.emails[i] === 'string')
            this.arrEmail.push(this.emails[i]);
      }
      const emailToString = this.arrEmail.toString();
      this.emailString = emailToString.replace(/,/g, ';');
    } else {
      this.emailString = null;
    }
  }

  close() {
    this.router.navigate(['/system/handle-report/', this.id, 'HUY']);
  }

  routeReport() {
    this.router.navigate(['/system/handle-report/create-report/', this.id, 'HUY']);
  }

  routeCancelInvoice() {
    this.router.navigate(['/system/handle-report/cancel-report/', this.id]);
  }

  checkStatusEmail() {
    this.checkedInputEmail = !this.checkedInputEmail;
  }

  getFileData(fileData) {
    this.fileData = fileData;
    if (this.fileData.length > 0) {
      for (let i = 0; i < this.fileData.length; i++) {
        this.fileData[i].type = 'DK';
      }
    }
  }
  startProcess_TBSaiSot() {
    if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
      || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
      this.getXml_TBSaiSot();
    } else {
      if (this.infoDangKy && this.infoDangKy.registration_signature) {
        let ListSerialNumber = "DSCKS";
        let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
        if (signatures && signatures.length > 0)
          ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
        let invoiceDate = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
        this.socketService.kiSo(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice', invoiceDate, "", ListSerialNumber, true);
      }
      else
        this.socketService.kiSo(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice')

    }
  }
  getXml_TBSaiSot() {
    let invoices = [];
    let object = new Object;
    object["tax_authority_code"] = this.dataInvoice.tax_authority_code;
    object["template_code"] = this.dataInvoice.template_code;
    object["invoice_series"] = this.dataInvoice.invoice_series;
    object["invoice_date"] = this.dataInvoice.invoice_date;
    object["invoice_number"] = this.dataInvoice.invoice_number;
    object["reason"] = this.form.value.invoice.destroy_reason;
    object["notification_nature"] = "1";
    if(this.dataInvoice.xml_type != "3"){
      object["type_apply_invoice"] = 3;
    }else{
      object["type_apply_invoice"] = 1;
    }
    object["is_invoice_with_code"] = this.dataInvoice.is_invoice_with_code;
    object["invoice_id"] = this.form.value.invoice.id;
    invoices.push(object);
    let data = {
      notification_type: 1,
      invoices: invoices,
    };
    let xmlInvoice = "";
    let sub = this.handleInvoiceService.getXmlInvoiceWrong(data).subscribe(rs => {
      if (rs.status == 'success') {
        xmlInvoice = rs.data.xml_file;
        this.invoices = rs.data.invoices;
        let sign_date = moment(rs.data.sign_date).format('YYYY-MM-DD HH:mm:ss');
        if (xmlInvoice != "") {
          if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
            || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing))
            this.sendData_TBSaiSot(xmlInvoice);
          else
            this.signXml_TBSaiSot(xmlInvoice, sign_date);
        }
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      sub.unsubscribe();
    });
  }
  sendData_TBSaiSot(xml_string) {
    let subXuatHoaDon = this.handleInvoiceService.sendXmlInvoiceWrong({
      xml_file: xml_string,
      invoices: this.invoices,
    }).subscribe(rs => {
      if (rs.status == 'success') {
        Library.notify(this._translate.NOTIFY.cancel_invoice_done, 'success');
        this.router.navigate(['/system/handle-report/', this.id, 'HUY']);
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      subXuatHoaDon.unsubscribe();
    });
  }
  signXml_TBSaiSot(xmlInvoice, signDate) {
    let nodeSign = "//TBao/DSCKS/NNT";
    let ListSerialNumber = "DSCKS";
    if (this.infoDangKy && this.infoDangKy.registration_signature) {
      let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
      if (signatures && signatures.length > 0)
        ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
    }
    this.socketService.kiSo(() => this.connectWebsocket(), xmlInvoice, 'hoadon.xml', nodeSign, signDate, 'NotificationData', ListSerialNumber, true);
  }
}
