import {Component, Input, OnInit} from '@angular/core';
import * as moment from 'moment';

@Component({
  selector: 'app-input-date-format',
  templateUrl: './input-date-format.component.html',
  styleUrls: ['./input-date-format.component.scss']
})
export class InputDateFormatComponent implements OnInit {
  @Input()
  dateInput: any;

  dateValue: any;

  constructor() {
  }

  ngOnInit() {
    this.displayDate(this.dateInput);
  }

  displayDate(date) {
    this.dateValue = moment(date).format('DD/MM/YYYY');
  }
}
