import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot} from '@angular/router';
import {Observable} from 'rxjs';
import {Library} from '@core/library';
import {map} from 'rxjs/operators';
import {HanldeReportService} from '@modules/handle-report/hanlde-report.service';

@Injectable({
  providedIn: 'root'
})
export class CreateReportResolverService implements Resolve<any[]> {

  constructor(
    private handleService: HanldeReportService,
    private router: Router
  ) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> | Promise<any[]> | any[] {
    Library.showloading();
    const id = route.paramMap.get('id');
    const status = route.paramMap.get('status');
    return this.handleService.getAllDataToCreateReport(id, status)
      .pipe(
        map(data => {
          Library.hideloading();
          if (data[0].status === 'success' && data[1].status === 'success' && data[2].status === 'success' && data[3].status === 'success') {
            return [
              data[0].data.invoice,
              data[1].data.invoice_reports,
              data[2].data.invoice_reports,
              data[3].data.configs
            ];
          } else {
            this.router.navigate(['/']);
          }
        })
      );
  }
}
