import {Component, OnInit} from '@angular/core';
import {HanldeReportService} from '@modules/handle-report/hanlde-report.service';
import {FormBuilder, FormGroup} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap';
import {Library} from '@core/library';
import {finalize} from 'rxjs/operators';
import {TranslateService} from '@ngx-translate/core';
import {AuthService} from '@core/services';

@Component({
  selector: 'app-cancel-report-modal',
  templateUrl: './cancel-report-modal.component.html',
  styleUrls: ['./cancel-report-modal.component.scss']
})
export class CancelReportModalComponent implements OnInit {
  form: FormGroup;
  id: any;
  data: any;
  outPutEmail: any;
  emails: any = [];
  private arrEmail: any = [];
  submitted = false;
  checkSubmit: any = false;
  _translate: any;
  _language: string;

  constructor(
    private authService: AuthService,
    private handleReportService: HanldeReportService,
    private fb: FormBuilder,
    public bsModalRef: BsModalRef,
    public translate: TranslateService
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    this.translate.use(this._language);
  }

  ngOnInit() {
    this.form = this.fb.group(this.handleReportService.generatedFormControlAll());
    this.form = this.data;
    this.outPutEmail = this.form.value.invoice_report.email;
    this.emails = [];
    if (this.outPutEmail !== null && this.outPutEmail !== undefined) {
      const stringDataEmailGet = this.outPutEmail.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.emails.push({display: stringDataEmailGet[i], value: stringDataEmailGet[i]});
      }
    }
  }

  submit() {
    if (this.form.get('invoice_report.email').invalid) {
      this.submitted = true;
      return;
    }
    this.submitted = false;

    Library.showloading();
    this.apiReport(this.form.value)
      .pipe(finalize(() => {
        Library.hideloading();
      }))
      .subscribe(
        value => {
          if (value.status === 'success') {
            this.form.patchValue({
              invoice_report: {
                id: value.data.invoice_report.id
              }
            });
            Library.notify(this._translate.NOTIFY.sign_and_send_success_report, 'success');
            this.bsModalRef.content.successSaveItem = 'SuccessCancelReport';
            this.bsModalRef.content.successSaveData = {
              email: this.form.value.invoice_report.email,
              id: value.data.invoice_report.id,
              signature_status: this.form.value.invoice_report.signature_status,
              seller_sign_name: value.data.invoice_report.hasOwnProperty('json_data') ? value.data.invoice_report.json_data.seller_sign_name : this.form.value.invoice_report.json_data.seller_sign_name,
            };
            this.bsModalRef.hide();
          } else {
            Library.notify(value.message, 'error');
          }
        },
        error => {
          Library.notify(error, 'error');
        }
      );
  }

  close() {
    this.bsModalRef.hide();
  }

  apiReport(data) {
    if (data.invoice_report.id !== null) {
      return this.handleReportService.apiUpdateReport(data.invoice_report.id, data);
    } else {
      return this.handleReportService.apiCreateReport(data);
    }
  }

  renderDataEmail($event) {
    this.arrEmail = [];
    this.emails = $event;

    if (this.emails.length > 0) {
      for (let i = 0; i < this.emails.length; i++) {
        if (typeof this.emails[i] === 'object')
        this.arrEmail.push(this.emails[i].value);
      else
        if (typeof this.emails[i] === 'string')
          this.arrEmail.push(this.emails[i]);
      }
      const emailToString = this.arrEmail.toString();
      this.form.patchValue({
        invoice_report: {
          email: emailToString.replace(/,/g, ';')
        }
      });
    } else {
      this.form.patchValue({
        invoice_report: {
          email: null
        }
      });
    }
  }
}
