import {Component, OnInit} from '@angular/core';
import {HanldeReportService} from '@modules/handle-report/hanlde-report.service';
import {FormBuilder, FormGroup} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap';
import {Library} from '@core/library';
import {finalize} from 'rxjs/operators';
import {AuthService} from '@core/services';
import {TranslateService} from '@ngx-translate/core';

@Component({
  selector: 'app-reload-report',
  templateUrl: './reload-report.component.html',
  styleUrls: ['./reload-report.component.scss']
})
export class ReloadReportComponent implements OnInit {
  id: any;
  email: any;
  arrEmail: any = [];
  form: FormGroup;
  submitted = false;
  checkSubmit: any = false;
  private outPutEmail: any = [];
  private dataEmail: any = [];
  _translate: any;
  _language: string;

  constructor(
    private authService: AuthService,
    public translate: TranslateService,
    private handleService: HanldeReportService,
    private fb: FormBuilder,
    public bsModalRef: BsModalRef,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    this.translate.use(this._language);
  }

  ngOnInit() {
    this.form = this.fb.group(this.handleService.formReloadReport);
    this.form.patchValue({
      id: this.id,
      email: this.email
    });
    this.arrEmail = [];
    if (this.email !== null) {
      const stringDataEmailGet = this.email.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.arrEmail.push({display: stringDataEmailGet[i], value: stringDataEmailGet[i]});
      }
    }
  }

  renderDataEmail($event) {
    this.outPutEmail = [];
    this.dataEmail = [];
    this.dataEmail = $event;

    if (this.dataEmail.length > 0) {
      for (let i = 0; i < this.dataEmail.length; i++) {
        if (typeof this.dataEmail[i] === 'object')
        this.outPutEmail.push(this.dataEmail[i].value);
      else
        if (typeof this.dataEmail[i] === 'string')
          this.outPutEmail.push(this.dataEmail[i]);
      }
      const emailToString = this.outPutEmail.toString();
      this.form.patchValue({
        email: emailToString.replace(/,/g, ';')
      });
    } else {
      this.form.patchValue({
        email: null
      });
    }
  }

  submit() {
    if (this.form.get('email').invalid) {
      this.submitted = true;
      Library.notify(this._translate.VALIDATION.enter_email_address_buyer, 'error');
      return;
    }
    this.submitted = false;
    this.checkSubmit = false;
    Library.showloading();
    this.handleService.apiReloadReport(this.form.value)
      .pipe(finalize(() => Library.hideloading()))
      .subscribe(value => {
        if (value.status === 'success') {
          this.bsModalRef.content.successSaveItem = 'Reload';
          this.bsModalRef.content.successSaveData = this.form.value.email;
          Library.notify(this._translate.NOTIFY.resend_report_success, 'success');
          this.bsModalRef.hide();
        } else {
          Library.notify(value.message, 'error');
          this.checkSubmit = true;
        }
      }, error => {
        Library.notify(error, 'error');
        this.checkSubmit = true;
      });
  }

  close() {
    this.bsModalRef.hide();
  }
}
