import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot} from '@angular/router';
import {Observable} from 'rxjs';
import {HanldeReportService} from '@modules/handle-report/hanlde-report.service';
import {Library} from '@core/library';
import {map} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ReportResolverService implements Resolve<any[]> {

  constructor(
    private router: Router,
    private handleService: HanldeReportService
  ) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> | Promise<any[]> | any[] {
    Library.showloading();
    const id = route.paramMap.get('id');
    const status = route.paramMap.get('status');
    return this.handleService.getAllDataToIndex(id, status)
    .pipe(
      map(data => {
        Library.hideloading();
        if (data[0].status === 'success') {
          return [
            data[0].data.invoice,
            data[1].data.invoice_reports[0]
          ];
        } else {
          if (sessionStorage.hasOwnProperty('urlIndex')) {
            if (sessionStorage.getItem('urlIndex') !== null) {
              const url = sessionStorage.getItem('urlIndex');
              this.router.navigate([url]);
            } else {
              this.router.navigate(['/']);
            }
          } else {
            this.router.navigate(['/']);
          }
        }
      })
    );
  }
}
